import { useState, useEffect } from "react";
import grabAndSlide from "../GrabAndSlide";
import styled from "styled-components/macro";
import { projects, TAG } from "../proejcts";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "../animation.css";
import { useSpring, animated } from "react-spring";

import jquery from "../assets/icons/Jquery.webp";
import react from "../assets/icons/react.webp";
import reactNative from "../assets/icons/reactnative.webp";
import vue from "../assets/icons/Vue.webp";
import nodeJS from "../assets/icons/nodejs.webp";
import {
  faChevronLeft,
  faChevronRight,
  faTicketAlt,
} from "@fortawesome/free-solid-svg-icons";

export default function About({
  setPage,
  broken,
  theme,
  setBroken,
  screenWidth,
}) {
  const [slide, setSlide] = useState({
    gcBoard: {
      index: 0,
    },
    movieHunt: {
      index: 0,
    },
    pokedex: {
      index: 0,
    },
    kokoatalk: {
      index: 0,
    },
    todo: {
      index: 0,
    },
    watsPoppin: {
      index: 0,
    },
  });
  const [sortBy, setSortBy] = useState("none");
  const [showDetail, setShowDetail] = useState("");
  const [brokenTicket, setBrokenTicket] = useState(800);
  const [showModal, setShowModal] = useState(false);

  const fadeIn_sortContainer = useSpring({
    config: { duration: 2000 },
    opacity: 1,
    from: { opacity: 0 },
  });

  const fadeIn_projectContainer = useSpring({
    config: { duration: 2000 },
    delay: 1000,
    opacity: 1,
    from: { opacity: 0 },
  });

  const fadeIn_ticket = useSpring({
    config: { duration: 2000 },
    delay: 2500,
    opacity: 1,
    from: { opacity: 0 },
  });

  useEffect(() => {
    grabAndSlide("app", 1.2);
  }, []);

  const renderModal = () => {
    return (
      <ModalBackDrop
        onClick={(e) => {
          const condition = Array.from(e.target.classList);
          if (condition.length !== 0) {
            if (condition[0].includes("BackDrop")) {
              setShowModal(false);
            }
          }
        }}
      >
        <Modal>
          <p>You need bigger screen to access this. (1118px +)</p>
        </Modal>
      </ModalBackDrop>
    );
  };

  const renderProjectSlide = (project, state) => {
    const length = project.images.length;
    if (project.tag.includes(sortBy) || sortBy === "none") {
      return (
        <Project
          kokoatalk={project.name === "kokoatalk"}
          theme={theme}
          style={fadeIn_projectContainer}
          screenWidth={screenWidth}
        >
          <button
            onClick={() => {
              setSlide((prev) =>
                prev[state].index === 0
                  ? {
                      ...prev,
                      [state]: { index: length - 1 },
                    }
                  : {
                      ...prev,
                      [state]: { index: prev[state].index - 1 },
                    }
              );
            }}
          >
            <FontAwesomeIcon icon={faChevronLeft} />
          </button>

          {project.images.map((item, index) => {
            return (
              <ProjectContent
                key={index}
                show={slide[state].index === index ? true : false}
              >
                <ProjectImg
                  src={item.default}
                  alt={project.name}
                  width={item.default.includes("kokoa") ? "20rem" : null}
                  data-name={project.name}
                  onMouseEnter={(e) => setShowDetail(e.target.dataset.name)}
                  show={project.name === showDetail}
                />
                <ProjectDescription
                  screenWidth={screenWidth}
                  show={
                    showDetail === project.name
                      ? false
                      : slide[state].index === index
                      ? true
                      : false
                  }
                >
                  <p>{project.description[index]}</p>
                </ProjectDescription>
                <ProjectDetails
                  show={project.name === showDetail}
                  kokoatalk={project.name === "kokoatalk"}
                  onMouseLeave={() => setShowDetail("")}
                  screenWidth={screenWidth}
                >
                  <div>
                    <h2>{project.name.toUpperCase()}</h2>
                  </div>
                  <div>
                    <section>
                      <h2>Summary</h2>
                      <div>{project.info.summary}</div>
                      <a
                        target="_blank"
                        href={project.project_src}
                        rel="noreferrer"
                      >
                        See App
                      </a>
                      <a
                        target="_blank"
                        href={project.project_code}
                        rel="noreferrer"
                      >
                        See Code
                      </a>
                    </section>
                    <Divider
                      show={project.name === "kokoatalk" ? false : true}
                    ></Divider>
                    <section>
                      <h2>Used Techs</h2>
                      <div>
                        {project.info.tech &&
                          project.info.tech.map((item, index) => (
                            <p key={index}>#{item}</p>
                          ))}
                      </div>
                    </section>
                  </div>
                </ProjectDetails>
              </ProjectContent>
            );
          })}

          <button
            onClick={() => {
              setSlide((prev) =>
                prev[state].index === length - 1
                  ? {
                      ...prev,
                      [state]: { index: 0 },
                    }
                  : {
                      ...prev,
                      [state]: { index: prev[state].index + 1 },
                    }
              );
            }}
          >
            <FontAwesomeIcon icon={faChevronRight} />
          </button>
        </Project>
      );
    } else {
      return null;
    }
  };

  return (
    <ProjectContainer className="app">
      {showModal && renderModal()}
      <SortContainer
        theme={theme}
        style={fadeIn_sortContainer}
        screenWidth={screenWidth}
      >
        <h2>Sort by</h2>
        <div>
          <SortIcon
            src={react}
            alt="react icon"
            onClick={() => setSortBy(TAG.REACT)}
            name={"react"}
            selected={sortBy}
            className={broken ? "fly1" : ""}
            theme={theme}
            screenWidth={screenWidth}
          />
          <SortIcon
            src={reactNative}
            alt="reactNative icon"
            onClick={() => setSortBy(TAG.REACT_NATIVE)}
            name={"reactNative"}
            selected={sortBy}
            className={broken ? "fly2" : ""}
            theme={theme}
            screenWidth={screenWidth}
          />
          <SortIcon
            src={nodeJS}
            alt="nodeJS icon"
            onClick={() => setSortBy(TAG.NODE_JS)}
            name={"nodeJS"}
            selected={sortBy}
            className={broken ? "fly3" : ""}
            theme={theme}
            screenWidth={screenWidth}
          />
          <SortIcon
            src={vue}
            alt="vue icon"
            onClick={() => setSortBy(TAG.VUE)}
            name={"vue"}
            selected={sortBy}
            className={broken ? "fly4" : ""}
            theme={theme}
            screenWidth={screenWidth}
          />
          <SortIcon
            src={jquery}
            alt="jquery icon"
            onClick={() => setSortBy(TAG.JQUERY)}
            name={"jquery"}
            selected={sortBy}
            className={broken ? "fly5" : ""}
            theme={theme}
            screenWidth={screenWidth}
          />
        </div>
        <h2
          onClick={() => setSortBy("none")}
          className={broken ? "fly6" : ""}
          theme={theme}
        >
          Clear
        </h2>
      </SortContainer>
      <ProjectSlider screenWidth={screenWidth}>
        {renderProjectSlide(projects.gcBoard, "gcBoard")}
        {renderProjectSlide(projects.movieHunt, "movieHunt")}
        {renderProjectSlide(projects.pokedex, "pokedex")}
        {renderProjectSlide(projects.kokoatalk, "kokoatalk")}
        {renderProjectSlide(projects.watsPoppin, "watsPoppin")}
        {renderProjectSlide(projects.todo, "todo")}
      </ProjectSlider>
      <ToTheater
        broken={broken}
        theme={theme}
        style={fadeIn_ticket}
        brokenTicket={brokenTicket}
        screenWidth={screenWidth}
      >
        <FontAwesomeIcon
          icon={faTicketAlt}
          onClick={() => {
            if (broken) {
              setBrokenTicket(brokenTicket - 15);
              if (brokenTicket <= 25) {
                setBrokenTicket(brokenTicket - 1);
                if (brokenTicket <= 3.5) {
                  if (screenWidth !== "true") {
                    setBroken(false);
                    setPage("theater");
                  } else {
                    setShowModal(true);
                  }
                }
              }
            } else {
              if (screenWidth === "normal") {
                setPage("theater");
              } else {
                setShowModal(true);
              }
            }
          }}
        />
      </ToTheater>
    </ProjectContainer>
  );
}

const ProjectContainer = styled.div`
  width: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  user-select: none;
`;

const SortContainer = styled(animated.section)`
  position: ${(props) =>
    props.screenWidth === "mobile" ? "relative" : "fixed"};
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-family: "Luckiest Guy", cursive;
  font-size: 1.6rem;
  margin-top: ${(props) => {
    if (props.screenWidth === "small") {
      return "5rem";
    } else if (props.screenWidth === "mobile") {
      return "5rem";
    } else {
      return "";
    }
  }};
  margin-left: ${(props) =>
    props.screenWidth === "mobile" || props.screenWidth === "small"
      ? "0"
      : "3vw"};
  color: ${(props) => (props.theme ? props.theme.normalFontColor : "black")};

  h2:nth-child(3) {
    cursor: pointer;
    border-radius: 0.3rem;
    padding: 0.25rem 1rem;
    border: ${(props) =>
      props.theme ? "3px solid white" : "3px solid rgba(0, 0, 0, 0.6)"};
    box-shadow: ${(props) =>
      props.theme
        ? "5px 6px 5px rgba(255, 255, 255, 0.3)"
        : "5px 6px 5px rgba(0, 0, 0, 0.8)"};
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 1.2rem;
    transition: 0.5s ease-in-out;

    &:active {
      transform: scale(0.95);
      box-shadow: ${(props) =>
        props.theme
          ? "3px 4px 1px rgba(255, 255, 255, 0.3)"
          : "3px 4px 1px rgba(0, 0, 0, 1)"};
      transition: 0.2s ease-in-out;
    }
  }
`;

const SortIcon = styled.img`
  width: ${(props) => {
    if (props.screenWidth === "mobile") {
      return "9vw";
    } else if (props.screenWidth === "small") {
      return "7vw";
    } else {
      return "4.5rem";
    }
  }};
  height: ${(props) => {
    if (props.screenWidth === "mobile") {
      return "9vw";
    } else if (props.screenWidth === "small") {
      return "7vw";
    } else {
      return "4.5rem";
    }
  }};
  min-width: 3.5rem;
  min-height: 3.5rem;
  margin: ${(props) => (props.screenWidth === "mobile" ? "0 1.5vw" : "0 1rem")};
  filter: ${(props) => props.theme && `drop-shadow(${props.theme.dropShadow})`};
  cursor: pointer;
  transition: 0.5s ease-in-out;
  opacity: ${(props) =>
    props.name === props.selected || props.selected === "none" ? 1 : 0.2};

  &:active {
    transform: scale(0.85);
    filter: ${(props) =>
      props.theme && `drop-shadow(${props.theme.dropShadowActive})`};
    transition: 0.2s ease-in-out;
  }
`;

const ProjectSlider = styled.section`
  margin-top: ${(props) => (props.screenWidth === "mobile" ? "0" : "8rem")};
  width: fit-content;
  height: 100vh;
  display: flex;
  flex-direction: ${(props) =>
    props.screenWidth === "mobile" ? "column" : "row"};
  padding: ${(props) =>
    props.screenWidth === "small" || props.screenWidth === "mobile"
      ? "5rem 0"
      : "5rem 1rem"};
  align-items: center;

  > div:nth-child(1) {
    margin: ${(props) => {
      if (props.screenWidth === "small") {
        return "5rem 8rem 0 2rem";
      } else if (props.screenWidth === "mobile") {
        return "0 0 4rem 0";
      } else {
        return "0 8rem";
      }
    }};
  }
`;

const Project = styled(animated.div)`
  display: flex;
  width: ${(props) => {
    if (props.kokoatalk) {
      return "fit-content";
    } else if (props.screenWidth === "small") {
      return "95vw";
    } else if (props.screenWidth === "mobile") {
      return "100%";
    } else {
      return "70rem";
    }
  }};
  height: fit-content;
  margin: ${(props) => (props.screenWidth === "mobile" ? "4rem 0" : "0 8rem")};
  padding: ${(props) => (props.screenWidth === "mobile" ? "0" : "1rem 1rem")};
  transition: 0.5s ease-in-out;

  > button {
    border: none;
    outline: none;
    height: fit-content;
    align-self: center;
    padding: ${(props) =>
      props.screenWidth === "mobile" ? "2vw" : "2rem 1rem"};
    font-size: ${(props) => (props.screenWidth === "mobile" ? "1rem" : "3rem")};
    transition: 0.4s;
    cursor: pointer;
    background-color: transparent;
    color: ${(props) => (props.theme ? props.theme.normalFontColor : "black")};

    &:active {
      font-size: 2.2rem;
      transition: 0.1s ease-in-out;
    }
  }
`;

const ProjectContent = styled.div`
  display: ${(props) => (props.show ? "block" : "none")};
  position: relative;
  height: 100%;
`;

const ProjectImg = styled.img`
  width: ${(props) => (props.width ? props.width : "100%")};
  height: 100%;
  z-index: ${(props) => (props.show ? -1000 : 1)};
  box-shadow: ${(props) =>
    props.theme ? "0px 0px 50px rgba(255, 255, 255, 0.6)" : ""};
`;

const ProjectDetails = styled.div`
  backdrop-filter: blur(1.2px);
  width: 100%;
  height: 100%;
  opacity: ${(props) => (props.show ? 1 : 0)};
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  z-index: ${(props) => (props.show ? 100 : -100)};
  display: flex;
  flex-direction: column;

  > div:nth-child(1) {
    width: 100%;
    display: flex;
    flex-direction: column;

    > h2 {
      text-align: center;
      font-size: ${(props) => {
        if (props.kokoatalk) {
          return "2rem";
        } else if (props.screenWidth === "small") {
          return "2.5vw";
        } else if (props.screenWidth === "mobile") {
          return "4vw";
        } else {
          return "3rem";
        }
      }};
      margin: ${(props) =>
        props.screenWidth === "mobile" ? "0.5rem 0 0.5rem 0" : "1rem 0 1rem 0"};
      text-decoration: underline;
    }
  }
  > div:nth-child(2) {
    overflow: hidden;
    display: ${(props) => (props.kokoatalk ? "block" : "flex")};
    justify-content: space-between;
    ${(props) =>
      props.kokoatalk &&
      "> section{ font-size: 0.9rem; padding: 0 !important;  >h2 {font-size: 1.3rem;}}"}

    > section {
      padding: ${(props) =>
        props.screenWidth === "small" || props.screenWidth === "mobile"
          ? "0 1.5vw"
          : "0 3rem"};
      width: 100%;
      text-align: center;
      height: fit-content;
      > div {
        padding: ${(props) => (props.kokoatalk ? "0 0.2rem" : "")};
        font-size: ${(props) => {
          if (props.kokoatalk) {
            return "0.9rem";
          } else if (props.screenWidth === "mobile") {
            return "2vw";
          } else if (props.screenWidth === "small") {
            return "1.5vw";
          } else {
            return "1rem";
          }
        }};
      }

      > h2 {
        text-decoration: underline;
        margin: ${(props) => {
          if (props.kokoatalk) {
            return "2rem 0 1rem 0";
          } else if (props.screenWidth === "mobile") {
            return "0 0 1rem 0";
          } else if (props.screenWidth === "small") {
          } else {
            return "0 0 2rem 0";
          }
        }};
        font-size: ${(props) => {
          if (props.kokoatalk) {
            return "1.3rem";
          } else if (props.screenWidth === "mobile") {
            return "3.2vw";
          } else if (props.screenWidth === "small") {
            return "2vw";
          } else {
            return "1.8rem";
          }
        }};
      }
      > a {
        color: white;
        margin: ${(props) =>
          props.screenWidth === "small" || props.screenWidth === "mobile"
            ? "1vw"
            : "2rem"};
        font-size: ${(props) => {
          if (props.kokoatalk) {
            return "0.9rem";
          } else if (props.screenWidth === "mobile") {
            return "2vw";
          } else if (props.screenWidth === "small") {
            return "1.5vw";
          } else {
            return "1rem";
          }
        }};
      }
    }

    section:nth-child(3) > div {
      display: grid;
      grid-template-columns: ${(props) =>
        props.kokoatalk ? "1fr 1fr" : "1fr 1fr 1fr"};
      ${(props) => props.kokoatalk && "grid-gap: 0; padding: 0 1rem;"}

      > p {
        margin: ${(props) =>
          props.screenWidth === "mobile" || props.screenWidth === "small"
            ? "0.2rem 0"
            : "0.5rem 0"};
      }
    }
  }
`;

const ProjectDescription = styled.div`
  backdrop-filter: blur(1.2px);
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  opacity: ${(props) => (props.show ? 1 : 0)};
  transition: 0.5s;
  > p {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(1.2px);
    color: white;
    margin: 0;
    padding: 0.3rem 0;
    top: ${(props) => (props.screenWidth === "mobile" ? "-7vw" : "-5rem")};
    width: 100%;
    font-size: ${(props) =>
      props.screenWidth === "mobile" ? "2vw" : "1.2rem"};
  }
`;

const Divider = styled.div`
  width: 2px;
  height: 20rem;
  background-color: rgba(255, 255, 255, 0.4);
  align-self: center;
  /* margin-bottom: -10rem; */
  display: ${(props) => (props.show ? "block" : "none")};
`;

const ToTheater = styled(animated.div)`
  width: 100vw;
  position: absolute;
  font-size: ${(props) => (props.broken ? `${props.brokenTicket}px` : "3rem")};
  bottom: 5vh;
  text-align: center;
  color: ${(props) => (props.theme ? props.theme.normalFontColor : "black")};
  display: ${(props) => (props.screenWidth === "mobile" ? "none" : "")};

  > svg {
    cursor: pointer;
  }
`;

const ModalBackDrop = styled.div`
  position: absolute;
  z-index: 1000;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Modal = styled.div`
  z-index: 2000;
  color: white;
  width: 45rem;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 3rem;
  padding: 2rem;
`;
